import installFactory from './vue-install'
import * as theme from './features/theme'

import Utils from './utils/index'
import Mixins from './mixins/index'
import Events from './features/events'
import ObigoRouter from './features/router'
import {install as Lockout} from './features/lockout'
// import { VueTouch } from './features/v-touch'

import appManager from './features/appManager'

const obigoUI = {
  version: '1.0.21',
  install: installFactory(),
  theme
}

const obigoGadgetUI = {
  version: '1.0.21',
  install: installFactory('gadget'),
  theme
}

export {
  Utils,
  Mixins,
  appManager,
  Events,
  Lockout,
  ObigoRouter,
  obigoGadgetUI
}

if (window && !window.obigoUI) {
  window.obigoUI = obigoUI
}

export default obigoUI



