import { current as theme } from './features/theme'
import { install as focusInstall } from './features/focus'

import { install as eventsInstall } from './features/events'
import { install as ambientLightInstall } from './features/ambientLight'
import { install as DeviceModelInstall } from './features/deviceModel'
import { getHardkeyInstance } from './features/hardkey'

import FocusDirective from './directives/obg-focus'
import SwipeDirective from './directives/touch-swipe'
import TapDirective from './directives/tap'

import {mouseEventPolyfill} from './utils/event'

export var Vue

function registerDirectives (_Vue, type = 'core') {
  _Vue.directive('obg-focus', FocusDirective)
  _Vue.directive('tap', TapDirective)

  if (type === 'core') {
    _Vue.directive('obg-touch-swipe', SwipeDirective)
  }
}

export default function installFactory (type = 'core') {
  return function (_Vue) {
    if (this.installed) {
      console.warn('Obigo-js-ui already installed in Vue.')
      return
    }

    Vue = _Vue

    eventsInstall(_Vue)
    DeviceModelInstall(_Vue)
    ambientLightInstall(_Vue)
    focusInstall(_Vue)
    registerDirectives(_Vue, type)
    mouseEventPolyfill()

    _Vue.prototype.$theme = theme
    _Vue.prototype.$hardkey = getHardkeyInstance()
  }
}



